.card{
  grid-column: 3 span;
  grid-row: 1 span;
  max-height: 520px;
  border-radius: 10px;
  position: relative;

  &__bac{
    display: none;
    height: 465px;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    background-color: rgba(54, 87, 203, 0.65);
    border-radius: 10px;
  }

  &__rating{
    position: absolute;
    top: 12px;
    right: 10px;
    z-index: 1;

    min-width: 62px;
    min-height: 33px;
    border-radius: 5px;
    background: var(--atlantis);

    display: flex;
    justify-content: center;
    align-items: center;
  }

  .rating-norm{
    background: var(--lemon);
  }

  .rating-low{
    background-color: var(--flush-mahogany);
  }

  &__btn{
    display: none;
    position: absolute;
    top: 39%;
    left: 17%;
    border: none;
    border-radius: 10px;
    background: var(--white);
    color: var(--cerulean-blue);
    font-size: 18px;
    font-style: var(--normal);
    font-weight: var(--fon700);
    line-height: 166.5%;
    box-shadow: 0px 0px 10px 0px rgba(55, 94, 232, 0.40);
    padding: 20px 40px;
    transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out;

    &:active{
      color: var(--white);
      background-color: var(--cerulean-blue);
    }
  }

  &__link-category{
    transition: color 0.3s ease-in-out;

    &:hover{
      color: var(--atlantis);
    }

    &:focus{
      color: var(--atlantis);
    }

    &:active{
      color: var(--cerulean-blue);
    }
  }

  img{
    border-radius: 10px;
    background-color: lightgray 50% ;
  }

  &__rating-text{
    color: var(--white);
    font-size: 18px;
    font-style: var(--normal);
    font-weight: var(--fon700);
    line-height: var(--normal);

  }

  &__picture{
    border-radius: 10px;
  }

  &__link{
    display: block;
    margin: 4px 0;
  }

  &:hover{

    .card__bac{
      display: none;
    }

    .card__btn{
      display: none;
    }
  }
}


.card-people{
  position: relative;

  img{
    border-radius: 10px;
    background-color: lightgray 50% ;
    max-width: 444px;
    width: 100%;
  }

  &__link{
    display: block;
    transition: color 0.3s ease-in-out;

    &:hover{
      color: var(--lemon);
    }

    &:focus{
      color: var(--lemon);
    }

    &:active{
      color: var(--cerulean-blue);
    }
  }

  &__position{
    position: absolute;
    top: 10px;
    left: 17px;
  }

  &__info{
    position: absolute;
    bottom: 40px;
    left: 37px;
  }

  .author-link{
    transition: color 0.3s ease-in-out;

    &:hover{
      color: var(--lemon);
    }

    &:focus{
      color: var(--lemon);
    }

    &:active{
      color: var(--cerulean-blue);
    }
  }

  &__text{
    margin-bottom: 6px;
  }
}

.card-news{
  position: relative;
  grid-column: 2 span;
  grid-row: 1 span;
  margin-left: -33px;

  &__info{
    position: absolute;
    display: flex;
    align-items: center;
    gap: 40px;
    top: 38px;
    left: 33px;
  }

  &__info-mini{
    position: absolute;
    top: 13px;
    left: 16px;
  }

  &__blog{
    position: absolute;
    bottom: 65px;
    left: 33px;
  }

  &__blog-mini{
    position: absolute;
    bottom: 23px;
    left: 15px;
  }

  img{
    border-radius: 10px;
  }

  &__bac{
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 99%;
    background-color: rgba(54, 87, 203, 0.65);
    border-radius: 10px;
    min-height: 185px;
  }

  &__btn{
    display: none;
    position: absolute;
    top: 35%;
    left: 17%;
    border: 2px solid var(--white);
    border-radius: 10px;
    background-color: rgba(0, 0, 0, 0);
    color: var(--white);
    font-size: 18px;
    font-style: var(--normal);
    font-weight: var(--fon700);
    line-height: 166.5%;
    box-shadow: 0px 0px 10px 0px rgba(55, 94, 232, 0.40);
    padding: 10px 25px;
    transition: color 0.3s ease-in-out, border 0.3s ease-in-out;

    &:active{
      color: var(--woodsmoke);
      border: 2px solid var(--woodsmoke);
    }
  }

  &__text{
    color: var(--white);
    font-size: 20px;
    font-style: var(--normal);
    font-weight: var(--fon500);
    line-height: 33px;
    max-width: 890px;
    max-height: 109px;
    margin-top: 13px;
  }

  &:hover{

    .card-news__bac{
      display: none;
    }

    .card-news__btn{
      display: none;
    }
  }
}


.card-mini{

  img{
    max-width: 95px;
    max-height: 141px;
  }

  &__content{
    margin-left: 13px;
    min-width: 148px;
  }
}

@media (max-width: 1500px){
  .card-news{
    margin: 0;
  }
}

@media (max-width: 1400px){

  .card{

    grid-column: 2 span;

    &__bac{
      height: 280px;
    }

    &__btn{
      font-size: 14px;
      padding: 13px 25px;
      top: 42%;
      left: 11%;
    }

    &__link{
      margin: 0;
    }

    &__rating{
      top: 10px;
      min-width: 45px;
      min-height: 25px;
    }

    &__rating-text{
      font-size: 12px;
    }
  }

  .card-mini{

    &__content{
      min-width: 0;
    }
  }

  .card-news{

    &__text{
      font-size: 16px;
    }
  }
}

@media (max-width: 800px){

  .card{

    &__bac{
      height: 100px;
    }
  }

  .card-mini{

    &__content{
      min-width: 0;
    }
  }

  .card-people{

    &__info{
      bottom: 21px;
      left: 27px;
    }
  }

  .card-news{

    &__text{
      display: none;
    }
  }
}


@media (max-width: 525px){

  .card{

    &__rating{
      min-width: 37px;
      min-height: 20px;
    }
  }
}
